import React, { useState } from "react";
import "./style.css";
import { QUERY_CATEGORIES, QUERY_USER } from "../../utils/queries";
import { ADD_PRODUCT } from "../../utils/mutations";
import { useMutation } from "@apollo/client";
import { useQuery } from "@apollo/client";
import {Cloudinary} from "@cloudinary/url-gen";


function CreateProduct({ trigger, setTrigger }) {
  const [formState, setFormState] = useState({});
  const { loading, data } = useQuery(QUERY_CATEGORIES);
  const [addProduct] = useMutation(ADD_PRODUCT);
  const data2 = useQuery(QUERY_USER);
  const myWidget = Cloudinary.createUploadWidget({
    cloudName: 'dzjvfg4wt', 
    uploadPreset: 'my_preset'}, (error, result) => { 
      if (!error && result && result.event === "success") { 
        console.log('Done! Here is the image info: ', result.info); 
      }
    }
  )
  
  document.getElementById("upload_widget").addEventListener("click", function(){
      myWidget.open();
    }, false);



  const handleFormSubmit = async (event) => {
    event.preventDefault();
    
    try {
      const { data } = await addProduct({
        variables: {
          name: formState.name,
          description: formState.description,
          image: formState.image,
          quantity: formState.quantity,
          price: formState.price,
          category: formState.category,
          user: data2.data.user._id,
        },
      });
      setTrigger(false);
    } catch (error) {
      console.error(error);
    }
  };
  const handleInputChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    console.log([name]);
    console.log(value);
    setFormState((values) => ({ ...values, [name]: value }));
  };

  return trigger ? (
    <div className="popupMessage">
      <p>Hello</p>
      <form className="popup-inner" onSubmit={handleFormSubmit}>
        <button className="close-btn" onClick={() => setTrigger(false)}>
          close x
        </button>
        <div className="flex-row space-between my-2">
          <label htmlFor="name">Product Name:</label>
          <input
            value={formState.name || ""}
            name="name"
            onChange={handleInputChange}
            type="text"
            placeholder="name"
          />
        </div>
        <div className="flex-row space-between my-2">
          <label htmlFor="Description">Description:</label>
          <input
            value={formState.description || ""}
            name="description"
            onChange={handleInputChange}
            type="text"
            placeholder="description"
          />
        </div>
        <div className="flex-row space-between my-2">
          <label htmlFor="Image">Image:</label>
          <input
            value={formState.image || ""}
            name="image"
            onChange={handleInputChange}
            type="file"
            placeholder="image"
          />
          <button id="upload_widget" class="cloudinary-button">Upload files</button>
          <script src="https://upload-widget.cloudinary.com/global/all.js" type="text/javascript"></script>
          

        </div>
        <div className="flex-row space-between my-2">
          <label htmlFor="Price">Price:</label>
          <input
            value={formState.price || ""}
            name="price"
            onChange={handleInputChange}
            type="String"
            placeholder="price"
          />
        </div>
        <div className="flex-row space-between my-2">
          <label htmlFor="Category">category:</label>
          <select
            value={formState.category}
            onChange={handleInputChange}
            name="category"
          >
            {loading ? (
              <option> still loading</option>
            ) : (
              data.categories.map((item) => (
                <option key={item._id} value={item._id}>
                  {item.name}
                </option>
              ))
            )}
          </select>
        </div>
        <div className="flex-row flex-end">
          <button type="submit" className="submit-msg">
            {" "}
            Add Product{" "}
          </button>
        </div>
      </form>
    </div>
  ) : (
    ""
  );
}

export default CreateProduct;
